.page-header__toggle-gamburger {
  position: relative;
  width: 24px;
  height: 12px;
  justify-self: end;
  align-self: center;
  margin-left: 20px;
  margin-right: 10px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #0b2330;
  z-index: 2;
  cursor: pointer;
}
.page-header__toggle-gamburger--closed::before {
  position: absolute;
  content: "";
  top: 3px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #0b2330;
}
.page-header__toggle-gamburger--closed::after {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;
  width: 16px;
  height: 2px;
  background-color: #0b2330;
}
.page-header__toggle-gamburger--opened {
  height: 18px;
  border-top: none;
}
.page-header__toggle-gamburger--opened::before,
.page-header__toggle-gamburger--opened::after {
  position: absolute;
  content: "";
  top: 7px;
  left: 2px;
  width: 25px;
  height: 2px;
  background-color: #0b2330;
}
.page-header__toggle-gamburger--opened::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header__toggle-gamburger--opened::after {
  transform: rotate(-45deg);
}
@media (min-width: 865px) {
  .page-header__toggle-gamburger {
    display: none;
  }
}
.page-header__toggle-gamburger--nojs {
  display: none;
}
.button-toggle {
